import React, { Component } from "react"
import Helmet from "react-helmet"
import Link from "gatsby-link"
import PropTypes from "prop-types"
import styled, { css } from "styled-components"
import {
  TERTIARY_TEXT,
  SECONDARY_TEXT,
  PRIMARY_TEXT,
  SMALLCHAT_SHADOW,
  SMALLCHAT_PURPLE,
  SMALLCHAT_BKG,
} from "../components/consts/colors"
import withScreensize from "../components/withScreensize"

const TermsWrapper = styled.div`
  display: flex;
  width: 100%;
  background-color: ${SMALLCHAT_BKG};
`
const TermsSidebar = styled.div`
  display: flex;
  flex-direction: column;
  width: 300px;
  margin-top: 50px;

  @media screen and (max-width: 980px) {
    width: 200px;
  }

  @media screen and (max-width: 768px) {
    display: none;
  }
`

const TermsLink = styled(Link)`margin: 20px 0px 0px 40px;`

const TermsContent = styled.div`
  display: flex;
  flex-direction: column;
  position: relative;
  width: 70%;
  background-color: white;
  border-radius: 16px;
  box-shadow: 0px 10px 30px ${SMALLCHAT_SHADOW};
  margin: 40px 0px 100px 0px;
  padding: 20px 40px;

  h1 {
    margin-bottom: 40px;
    margin-top: 40px;
    &:first-child {
      margin-top: 0px;
    }
  }
  h3 {
    margin-top: 20px;
    margin-bottom: 20px;
  }
  i {
    margin-top: 100px;
    margin-bottom: 40px;
  }

  @media screen and (max-width: 980px) {
    width: 65%;
  }
  @media screen and (max-width: 768px) {
    width: 80%;
    justify-content: center;
    margin: 0 auto;
    margin-top: 50px;
    margin-bottom: 100px;
    padding: 10px 30px;
  }

  @media screen and (max-width: 480px) {
    width: 90%;
    padding: 5px 20px;

    margin-bottom: 50px;
  }
`

function Terms({ screensize }) {
  const isMobile = screensize === "phone"
  const isTablet = screensize === "tablet"

  return (
    <div>
      <Helmet title="Terms & Privacy Policy" />
      <TermsWrapper>
        <TermsSidebar>
          <TermsLink to="/terms/Privacy">Terms of Service</TermsLink>
          <TermsLink to="/terms/Privacy">Privacy Policy</TermsLink>
        </TermsSidebar>
        <TermsContent>
          <h1>Terms of Service</h1>
          <h3>Terms</h3>
          <p>
            These Terms of Service (“Terms“) govern your use of the free
            services, software and websites (the “Service”) provided by
            Smallchat. By using the Service you agree to be bound by these
            Terms.
          </p>
          <p>
            If you’re using our Service on behalf of an organization or entity
            (“Organization”), then you are agreeing to these Terms on behalf of
            that Organization and you represent and warrant that you have the
            authority to bind the Organization to these Terms. In that case,
            “you” and “your” refers to that Organization.
          </p>
          <p>
            Our Service is not intended for use by persons under the age of 18.
            By using the Service you are representing to us that you are over
            the age of 18.
          </p>
          <h3>Content</h3>
          <p>
            The Service allows you to send messages, text, files and other
            materials (together the “Content”) to others. You retain ownership
            of your Content, but by uploading it onto the Service, you are
            granting us a license to use, copy, reproduce, process, adapt,
            publish, transmit, host and display that Content for any purpose.
          </p>
          <p>
            We reserve the right to remove Content on the Service for any
            reason.
          </p>
          <h3>Property, Modifications, and Termination</h3>
          <p>
            The Service is provided “as is”. Smallchat does not warrant or make
            any representations concerning the accuracy, likely results, or
            reliability of the use of the software on its web site.
          </p>
          <p>
            The Service constitutes Smallchat’s exclusive intellectual property.
            Any feedback, comments, or suggestions you may provide regarding the
            Service is entirely voluntary and we will be free to use such
            feedback, comments or suggestions as we see fit and without any
            obligation to you.
          </p>
          <p>
            The Service may be modified from time to time, often without prior
            notice to you. Your continued use of the Service constitutes your
            acceptance of such modifications. If you are not satisfied with a
            modification we make to the Service, your sole remedy is to
            terminate your use of the Service.
          </p>
          <p>
            You are free to stop using our Service at any time. We also reserve
            the right to suspend or end the Service at any time at our
            discretion and without notice. We may also terminate or suspend your
            access to the Service at any time for any reason.
          </p>
          <h3>Limitations</h3>
          <p>
            In no event shall Smallchat or Nicer LLC or its employees be liable
            for any damages, including direct, indirect, punitive, incidental,
            special, consequential, or exemplary damages (including, without
            limitation, damages for loss of data or profit, goodwill, intangible
            losses, or due to business interruption) arising out of the use or
            inability to use the Service, even if Smallchat has been notified
            orally or in writing of the possibility of such damage. Smallchat’s
            aggregate liability for all claims related to the service shall not
            exceed $100 or the amounts paid by you to Smallchat in connection
            with your use of the Service during the twelve month period prior to
            the claim giving rise to such liability.
          </p>
          <p>
            The Service is controlled, operated and hosted from within the U.S.
            We make no representations that the Service is appropriate or
            available for use in other locations. Those who access or use the
            Service from other jurisdictions do so at their own risk and are
            responsible for compliance with all applicable United States and
            local laws and regulations, including but not limited to export and
            import regulations.
          </p>
          <h1>Privacy Policy</h1>
          <p>
            Your privacy and data are important to us. Our privacy policy is as
            follows:
          </p>
          <h3>Data we access and store</h3>
          <ul>
            <li>
              Through the Smallchat Slack integration, we access and store
              information about your team’s members for the purpose of
              displaying that information to visitors you chat with. That
              information includes but is not limited to full names and profile
              pictures. We also have access to and may store, but do not display
              to visitors, data available through the Slack oauth scopes that
              Smallchat accesses. Those scopes include:
            </li>
            <ul>
              <li>Channels:write</li>
              <li>Groups:write</li>
              <li>Bot</li>
              <li>Commands</li>
              <li>Users:read</li>
              <li>Team:read</li>
            </ul>
            <p>
              The data available through these scopes can be reviewed in Slack’s{" "}
              <a href="https://api.slack.com/docs/oauth-scopes">
                API documentation:
              </a>
            </p>
            <li>
              We may access and store email addresses from users of the Service.
              We do not sell those email addresses but may use them to send
              information about the Service, Nicer LLC, maintenance, changes to
              the Service, and new features.
            </li>
            <li>
              We will collect personal information by lawful and fair means and,
              where appropriate, with the knowledge or consent of the individual
              concerned.
            </li>
            <li>
              We will protect personal information by reasonable security
              safeguards against loss or theft, as well as unauthorized access,
              disclosure, copying, use or modification.
            </li>
            <li>
              We will make readily available to customers information about our
              policies and practices relating to the management of personal
              information.
            </li>
          </ul>
          <i>
            We are committed to ensuring that the confidentiality of personal
            information is protected and maintained.
          </i>
        </TermsContent>
      </TermsWrapper>
    </div>
  )
}

export default withScreensize(Terms)



